const buttonEl = document.querySelector('year__btn');
const inputEl = document.querySelector('year__input');
const resoultEl = document.querySelector('year__resoult');
buttonEl.addEventListener('click', () => {
    if(isNaN(inputEl.value) || inputEl.value === ''){
        resoultEl.innerHTML='ви гн ввели чесло';
        resoultEl.style.color=localStorage.getItem('theme') === 'true' ? '#fff' : '#000';
    }
    else{
        resoultEl.innerHTML = inputEl.value %4 === 0 ? 'вии народились у високосний рік' : 'ви народились не у високосний рік';
        resoultEl.style.color = inputEl.value %4 === 0 ? 'red' : 'green';
        inputEl.value = '';
    }
})
